<!--我的课表-->
<template>
	<div class="school">
		<div class="title">我的课表</div>
		<div class="container">
			<el-row>
				<el-col :span="24">
					<div class="content">
						<div class="top">
							<timeSelect class="time-select" @time-change="timeSelected"></timeSelect>
						</div>
						<syn-calendar :tableData="lists" :type="6" :current="date"></syn-calendar>
					</div>
				</el-col>
			</el-row>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				date: new Date(),
				userId: '1186095737562202114',
				lists: []
			}
		},
		components: {
			timeSelect: () => import("@/components/time-selected"),
			synCalendar: () => import("@/components/calendar")
		},
		created() {
			this.getMyOwnClass()
		},
		methods: {
			/* 我的课表 */
			getMyOwnClass() {
				const params = {
					date: this.date,
					userId: this.userId
				}
				this.$api.getMyOwnClass(params).then(res => {
					this.lists = res.data;
					this.lists.map(v => {
						v.date = this.getWeekDay(v.courseDate)
					})
				})
			},
			getWeekDay(params) {
				let time = new Date(params).getDay()
				return time == 0 ? 7 : time
			},
			timeSelected(value) {
				this.date = value;
				this.getMyOwnClass();
			}
		}
	}
</script>

<style lang="scss" scoped>
	.school {
		padding: 32px;
	}

	.container {
		margin-top: 20px;
	}

	.container {
		.content {
			background: #fff;
			border-radius: 6px;
			padding: 23px;
			padding-top: 13px;

			.top {
				text-align: right;
				margin-bottom: 30px
			}

			.top .time-select {
				display: inline-block;
				margin: 0;
			}
		}
	}
</style>
